export function markUpGallery(imagesArray, gallery) {
  const makeGalleryCard = ({
    webformatURL,
    largeImageURL,
    tags,
    likes,
    views,
    comments,
    downloads,
  }) =>
    `
<a class="gallery__item" href="${largeImageURL}">
<div class="photo-card">
  <img src="${webformatURL}" alt="${tags}" loading="lazy" class="photo-card__image" />
  <div class="info">
    <p class="info-item">
      <b>Likes</b>${likes}
    </p>
    <p class="info-item">
      <b>Views</b>${views}
    </p>
    <p class="info-item">
      <b>Comments</b>${comments}
    </p>
    <p class="info-item">
      <b>Downloads</b>${downloads}
    </p>
  </div>
</div>
</a>
    `;
  // console.log(dataArray.hits);
  const markup = imagesArray.hits.map(item => makeGalleryCard(item)).join('');
  gallery.insertAdjacentHTML('beforeend', markup);
}
